.IconBox {
  display: inline-block;
  min-width: 160px;
  padding: 1rem;
  background-color: gray;
  text-align: center;
  -webkit-animation: fadein 1s;
          animation: fadein 1s; }
  .IconBox svg {
    display: inline-block; }
  .IconBox .name {
    color: white;
    font-size: 9pt; }

.IconBox + .IconBox {
  margin-left: 0.5rem; }

@-webkit-keyframes fadein {
  0% {
    -webkit-transform: translateX(1000px);
            transform: translateX(1000px);
    opacity: 0; }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1; } }

@keyframes fadein {
  0% {
    -webkit-transform: translateX(1000px);
            transform: translateX(1000px);
    opacity: 0; }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1; } }



